.header { 
  width: 100%;
  height: 120px;
  display: flex;
  justify-content: space-evenly;
  border-bottom: 2px solid #4D76A1;
}

.header__logo {
  align-self: center;
  width: 174px;
  height: 60px;
  pointer-events: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none; 
  user-select: none;

}